<template>
  <div class="">
    <div class="table-infinite-box" v-bind:class="{repoatwidth:isrepoatwidth}">

      <!-- 头部 -->
      <div class="table-infinite-top">
        <div class="table-normal-div">
          <div :style="{width:indexCol.width}" v-if="indexCol.isNeed">{{indexCol.name}}</div>
          <div v-for="(item,index) of header" :key="index" :style="{width:item.width}" class="table_header">{{item.name}}</div>
          <div :style="{'width':operateCol.width,'min-width':operateCol.minWidth}">{{operateCol.name}}</div>
        </div>
      </div>

      <!-- 内容 -->
      <div class="table-infinite-content">
        <div class="" v-for="(item,index) of data" :key="index">

          <!--展示数据  -->
          <div class="table-normal-div" v-if="index!=curEdit">
            <div :style="{width:indexCol.width}" v-if="indexCol.isNeed">{{index+1}}</div>
            <div v-for="(head,headindex) in header" :key="headindex" :style="{width:head.width}">
              {{item[head.value]}}
            </div>
            <!-- 操作 -->
            <div :style="{'width':operateCol.width,'min-width':operateCol.minWidth}">
              <a class="table-link" @click="editItem(index)">编辑</a>
              <a class="table-link" @click="delItem(index)" v-if="!componentData.cancleDeleteBtn">删除</a>
            </div>
          </div>

          <!-- 编辑状态 -->
          <div class="table-normal-div" v-else>
            <div :style="{width:indexCol.width}" v-if="indexCol.isNeed">{{index+1}}</div>
            <div class="module_ckmx_td_input" v-for="(head,headindex) of header" :key="headindex" :style="{width:head.width}">
              <el-input v-if="head.type=='text'" v-model="item[head.value]" placeholder="请输入"></el-input>
              <el-input v-if="head.type=='number'" v-model="item[head.value]" placeholder="请输入"></el-input>
              <el-input v-if="head.type=='select'" v-model="item[head.value]" placeholder="请输入"></el-input>
              <el-date-picker class='header-right-time' v-if="head.type=='time'" type="date" :editable='false' v-model="item[head.value]" placeholder="选择时间" :clearable='true' value-format="yyyy-MM-dd" >
              </el-date-picker>
            </div>
            <div :style="{'width':operateCol.width,'min-width':operateCol.minWidth}">
              <a class="table-link" @click="saveItem(index)">保存</a>
              <a class="table-link" @click="cancleItem(index)">取消</a>
            </div>
          </div>

        </div>
      </div>
      
      <!-- 底部 -->
      <div class="table-infinite-footer" v-if="!componentData.cancleFooter">
        <div class="table-normal-div">
          <div :style="{width:indexCol.width}" v-if="indexCol.isNeed">{{data.length+1}}</div>
          <div class="module_ckmx_td_input" v-for="(item,index) of header" :key="index" :style="{width:item.width}">
            <el-input v-if="item.type=='text'||item.type=='number'" v-model="curNewdata[item.value]" placeholder="请输入"></el-input>
            <el-date-picker class='header-right-time' v-if="item.type=='time'" type="date" :editable='false' v-model="curNewdata[item.value]" placeholder="选择时间"  :clearable='true' value-format="yyyy-MM-dd" >
            </el-date-picker>
            <el-select v-if="item.type=='select'" v-model="curNewdata[item.value]" filterable remote :clearable='true' reserve-keyword placeholder="请输入" :remote-method="remoteMethod" :loading="loading" class='header-right-time header-right-time-slect'>
              <el-option v-for="item in options4" :key="item.name" :label="item.name" :value="item.name">
              </el-option>
            </el-select>
          </div>
          <div :style="{'width':operateCol.width,'min-width':operateCol.minWidth}">
            <a class="table-link" @click="addItem()">添加</a>
            <a class="table-link" @click="clearNew()">清除</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getcomPany,getNewCompanyNews} from '../../config/api'
  import {
    dialogConfirm,
    dialogConfirmHasbtn,
    compareJson
  } from '../../config/mUtils'
  export default {
    inject: ['reload'],
    data() {
      return {
        curEdit: -1,
        curItem: '',
        curNewdata: '',
        canNav: true,

        data:this.componentData.data,
        header:this.componentData.header,
        newdata:this.componentData.newdata,
        operateCol:this.componentData.operateCol,
        indexCol:this.componentData.indexCol,
        // cancleFooter:this.componentData.cancleFooter,
        // cancleFooter:this.componentData.cancleFooter
        status : false,

        loading: false,
        options4: [],
        isrepoatwidth: false,
      }
    },
    props: ['componentData'], //type=>1出款 2回款
    components: {
      
    },
    created() {
    	  if (this.componentData.flag) {
    	  	this.isrepoatwidth = true
    	  }
      this.curNewdata = JSON.parse(JSON.stringify(this.newdata));
      // this.$emit("ee", "");
    },
    computed: {},
    mounted() {
  
    },
    methods: {
      //编辑
      editItem(index) {
        
        if(this.curEdit==-1){
          this.curEdit = index
          this.curItem = JSON.parse(JSON.stringify(this.data[index]));
        }else{
          this.saveItem(this.curEdit);
          if(this.status){
            this.curEdit = index
            this.curItem = JSON.parse(JSON.stringify(this.data[index]));
          }
          // if(this.saveItem(this.curEdit)){
            
          // }
          
        }
      },
      //保存
      saveItem(index) {
        if(this.checknum(this.data[index])){
          this.curEdit = -1;
           this.$emit("add",index);
           this.status = true;
        }else{
          this.status = false;
        }
        
      },
      //取消
      cancleItem(index) {
        this.curEdit = -1;
        this.data[index] = JSON.parse(JSON.stringify(this.curItem));
      },
      //删除
      delItem(index) {
        // console.log(index);
        dialogConfirmHasbtn('确定要删除该行').then(() => {
          this.$emit("delete",index);
          this.data.splice(index,1);
        }).catch(() => {});
        // console.log(this.$emit);
        // this.$emit("delete",index);
      },
      //添加
      addItem() {
        if(this.checknum(this.curNewdata)){

          let newObj = JSON.parse(JSON.stringify(this.curNewdata));
          this.data.push(newObj);
          this.curNewdata = JSON.parse(JSON.stringify(this.newdata))
          this.$emit("add",'');
          // console.log(newObj)
        }
      },
      //清除
      clearNew() {
        this.curNewdata = JSON.parse(JSON.stringify(this.newdata))
      },
  
      //验证格式
      checknum(checkObj) {
        // console.log(checkObj);
        let canContinue = true;
        this.header.forEach((item, headerIndex) => {
          let e = checkObj[item.value];
          // console.log(checkObj);
          
          if (canContinue) {
            if (item.is_must) {
              
              if (e == '') {
                dialogConfirm('info', '存在未填项');
                canContinue = false;
              } else {
                //number格式
                if (item.type == 'number') {
                  let testnumber = /(^[1-9]([0-9]+)?(\.[0-9]{1,10})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/gi.test(e);
                  if (!testnumber) {
                    dialogConfirm('info', item.name + '格式不正确,请输入整数或小数');
                    canContinue = false
                  }
                }
              }
            } else {
              //number格式
              if (item.type == 'number') {
                let testnumber = /(^[1-9]([0-9]+)?(\.[0-9]{1,10})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/gi.test(e);
                if (!testnumber) {
                  dialogConfirm('info', item.name + '格式不正确,请输入整数或小数');
                  canContinue = false
                }
              }
            }
          }
        });
        return canContinue;
      },

      //搜索内容
      remoteMethod(query) {
        if (query !== '') {
          this.loading = true;
          getcomPany({
            name: query
          }).then(res => {
            console.log(res.data.payload);
            this.loading = false;
            if (res.data.msgNo == 1) {
              if (res.data.payload.length == 0) {
                this.options4 = [{
                  name: query
                }]
              } else {
                let newObj = {
                  name: query
                }
                res.data.payload.unshift(newObj);
                this.options4 = res.data.payload;
              }

            }
          })
        } else {
          this.options4 = [];
        }
      },

      // changeName(name){
      //   console.log(name);
      //   // console.log(this.options4);
      //   if(this.options4.length>1){
      //     getNewCompanyNews({
      //       company_name:name,
      //     }).then(res=>{
      //       console.log(res);
      //     })
      //   }
      // }
    }
  }
</script>

<style lang="scss" scoped>
@import '../../style/folder.scss';
@import '../../style/fund1.scss';
.repoatwidth{
	width: 2000px;
}
</style>



