		watch: {},

		components: {
			editTable,
			normalTable,
			// investorReportView,
		},
		created() {

			if(this.$route.query.fundId) {
				this.fundId = this.$route.query.fundId;
			}
			if (this.$route.query.page) {
				this.page = this.$route.query.page;
			}
			if (this.$route.query.type) {
				this.type = this.$route.query.type;
			}
			if(getStore('needGetData') == 'true') {
				if(getStore('pdfData')) {
					let pafData = JSON.parse(getStore('pdfData'));
					this.fund_base_info = pafData.fund_base_info;
					this.companyData = pafData.companyData;
					this.company_count = pafData.company_count;
					this.partnerTable = pafData.partnerTable;
					this.projectDetailData = pafData.projectDetailData;
					this.projectTableData = pafData.projectTableData;
					this.evaluate = pafData.evaluate;
					this.companyQuite = pafData.companyQuite;
					this.company_quite = pafData.company_quite;
				} else {
					this.reportCreateData(this.fundId);

				}
			} else {
				this.reportCreateData(this.fundId);
			}

		},
		mounted() {

		},
		computed: {},
		methods: {
			// toOwnCloud() {
			//   // import { addSignature } from '../../config/mUtils'
			//   let params = {
			//     phone: '17802118897',
			//     return_url: 'http://localhost:8080/#/home/investorReport',
			//     show_name: '默认文件夹'
			//   }
			//   // params = addSignature(params);
			//   let url = '';
			//   let newArr = Object.keys(params);
			//   newArr.forEach((element, index) => {
			//     let str = element + '=' + params[element];
			//     if (index != newArr.length - 1) {
			//       str = str + '&';
			//     }
			//     url += str;
			//   });
			// },
			reportCreateData(id) {
				reportCreate({
					fund_id: id,
					type: this.type
				}).then(res => {
					let data = res.data.payload;
					//基金主题信息
					this.fund_base_info = data.fund_base_info;
					this.fund_base_info.invest = this.fund_base_info.invest || 0;
					this.fund_base_info.duration1 = this.fund_base_info.duration[0];
					this.fund_base_info.duration2 = this.fund_base_info.duration[1];
					this.fund_base_info.duration3 = this.fund_base_info.duration[2];
					// data.company_quite = {
					//     369:{
					//       type:1,
					//       invest:'11',
					//       surplus:'11',
					//       cost:'11',
					//       income:'11',
					//       profit:'11',
					//       outIncome:'11',
					//       investRate:'11',
					//       investTime:'2001-11-28',
					//       outTime:'2001-11-28',
					//       days:'11',
					//       annualRate:'11',
					//       IRR:'11',
					//       summary:'111',
					//     }
					//   }
					//基金运作概况
					this.company_count = {
						ytxm: data.company_count['已投项目'] || 0,
						qbtc: data.company_count['全部退出'] || 0,
						bftc: data.company_count['部分退出'] || 0,
						tcsyl: data.company_count['退出收益率'] || 0,
						jdtz: data.company_count['本季度投资项目'] || 0,
						jdtc: data.company_count['本季度退出项目'] || 0,
					}

					data.company_list.forEach((item, index) => {
						//项目主体信息
						let projectTableData_data = {
							name: item.name,
							mainBusiness: item.mainBusiness,
							field: item.field,
							regLocation: item.regLocation,
							thisYearProfile: item.thisYearProfit,
						}
						this.projectTableData.data.push(projectTableData_data); //项目主体信息
						//项目投资明细
						let projectDetailData_data = {
							name: item.name,
							figure: data.company_detail[item.id].figure || 0,
							stock_number: 0,
							stock_scale: item.proportion || 0,
						}
						this.projectDetailData.data.push(projectDetailData_data); //项目投资明细

						// 公司
						let companyData_data = {
							name: item.name,
							financialData: JSON.parse(JSON.stringify(this.financialData)),
							news: JSON.parse(JSON.stringify(this.news)),
							traceRecord: JSON.parse(JSON.stringify(this.traceRecord)),
							importRecord: JSON.parse(JSON.stringify(this.importRecord)),
							baseInfo: {
								regLocation: item.regLocation,
								estiblishTime: item.estiblishTime,
								mainBusiness: item.mainBusiness,
								regCapital: item.regCapital,
								legalPersonName: item.legalPersonName,
								invest_time: item.invest_time,
								proportion: item.proportion,
								figure: data.company_detail[item.id].figure,
								manager: '',
							}
						}
						companyData_data.news.data = data.company_news[item.id].news;
						companyData_data.traceRecord.data = data.company_news[item.id].trace_record;
						companyData_data.importRecord.data = data.company_news[item.id].import_record;
						this.companyData.push(companyData_data); // 公司具体数据
						//项目退出数据处理
						if(!Array.isArray(data.company_quite)) {
							if(data.company_quite[item.id]) {
								let newObj = {
									name: item.name,
									cost: data.company_quite[item.id].cost,
									income: data.company_quite[item.id].income,
								};
								if(data.company_quite[item.id].type == 1) {
									newObj.type = '部分退出';
									data.company_quite[item.id].type = '部分退出'
								} else {
									newObj.type = '全部退出';
									data.company_quite[item.id].type = '全部退出'
								}
								this.companyQuite.data.push(newObj);

								let newObj1 = {
									name: item.name,
									data: data.company_quite[item.id]
								};
								this.company_quite.push(newObj1);
							}
						}
					});
				})
			},
			exportData() {
				this.$router.push({
					name: 'investorReportView',
					query: {
						fundId: this.fundId,
						type: this.type
					}
				})
				let content = {
					fund_base_info: this.fund_base_info,
					company_count: this.company_count,
					partnerTable: this.partnerTable,
					projectTableData: this.projectTableData,
					projectDetailData: this.projectDetailData,
					companyData: this.companyData,
					evaluate: this.evaluate,
					companyQuite: this.companyQuite,
					company_quite: this.company_quite,
				}
				setStore('pdfData', JSON.stringify(content));
			},
			toAddCompany(index) {

				if(typeof index != 'number') {
					let dataObj = this.projectTableData.data[this.projectTableData.data.length - 1];
					let companyData_data = {
						name: dataObj.name,
						financialData: JSON.parse(JSON.stringify(this.financialData)),
						news: JSON.parse(JSON.stringify(this.news)),
						traceRecord: JSON.parse(JSON.stringify(this.traceRecord)),
						importRecord: JSON.parse(JSON.stringify(this.importRecord)),
						baseInfo: {
							regLocation: dataObj.regLocation,
							estiblishTime: '',
							mainBusiness: dataObj.mainBusiness,
							regCapital: '',
							legalPersonName: '',
							invest_time: '',
							proportion: '',
							figure: '',
							manager: '',
						}
					}

					getNewCompanyNews({
						company_name: dataObj.name,
					}).then(res => {
						let resData = res.data.payload;

						if(!Array.isArray(resData)) {
							companyData_data.news.data = resData.company_news.news;
							companyData_data.traceRecord.data = resData.company_news.trace_record;
							companyData_data.importRecord.data = resData.company_news.import_record;
						}

						this.projectDetailData.data.push({
							name: dataObj.name,
							figure: '',
							stock_number: '',
							stock_scale: '',
						})
						this.companyData.push(companyData_data);
					})

				} else {
					this.projectDetailData.data[index].name = this.projectTableData.data[index].name;
					this.companyData[index].name = this.projectTableData.data[index].name;
					this.companyData[index].baseInfo.regLocation = this.projectTableData.data[index].regLocation;
					this.companyData[index].baseInfo.mainBusiness = this.projectTableData.data[index].mainBusiness;
					this.companyData[index].baseInfo.figure = this.projectDetailData.data[index].figure;
					this.companyData[index].baseInfo.proportion = this.projectDetailData.data[index].stock_scale;
				}
			},
			toDeleteCompany(index) {
				this.projectTableData.data.splice(index, 1);
				this.projectDetailData.data.splice(index, 1);
				this.companyData.splice(index, 1);
			}
		}
	};
</script>

<style lang="scss" scoped>
	// @import '../../style/modal.scss';
