		// width: 1200px;
		
		overflow-x: scroll;
		white-space: nowrap;
	}
	.major_finance_data .table-infinite-box{
		width: 2000px !important;
	}
	.downLoadBtn {
		font-size: 14px;
		color: #3C98E8;
		border: 1px solid #3C98E8;
